.conte {
    position: absolute;
    width: 100%;
    min-height: 100vh;
    background-image: url('../../access/Fondos.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: -1;
}
.filOrIm {
    display: flex;
  align-items: center;
  margin-bottom: 10px;
  justify-content:space-evenly;
  margin-top: 30px;
  margin-bottom: 30px;
}
  
.conetFIlOr {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 10px;
   
  }
  
 
  .image {
    justify-content: flex-start;
    width: 150px; /* Ajusta el ancho de la imagen según tus necesidades */
    height: auto; /* Ajusta la altura de la imagen según tus necesidades */
